                  ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-select
                    v-model="editedItem.data_endian"
                    label="data endian"
                    :items="point_data_endians"
                    required
                    :rules="[rules.required]"
                  ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field
                    v-model="editedItem.register"
                    label="register"
                    :rules="[() => !isExistingvalue(editedItem, 'register') || 'Register Existed']"
                    type="number"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field
                    v-model="editedItem.register_length"
                    label="register length"
                    type="number"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field value="Point round/math" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.value" label="value" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.value_round" label="value round" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.value_operation" label="math operation"> </v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.value_raw" label="value raw"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field v-model="editedItem.fallback_value" label="Fallback Value" type="number" hint="The empty value will return null"></v-text-field>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field value="Point value scale" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.input_min" label="input_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.input_max" label="input_max" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.scale_min" label="scale_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.scale_max" label="scale_max" type="number"></v-text-field>
                </v-col>
                </v-row>

            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import wizard dialog-->
    <v-dialog v-model="dialogImport">
      <v-toolbar flat>
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="closeDialogImport">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1"> Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2"> Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn x-small class="mr-1 btn-height-22 white--text" color="primary" @click="clickContinue()" :disabled="!dataImport">Continue</v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="validWizard" ref="form_wizard" lazy-validation>
              <v-data-table
                :headers="advancedHeaders"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="tableProps.FOOTER_PROPS"
                :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                :search="searchWizard"
                :loading="savingImport"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field
                      v-model="searchWizard"
                      append-icon="mdi-magnify"
                      label="Search"
                      single-line
                      hide-details
                    ></v-text-field>
                  </v-toolbar>
                </template>
                <template v-slot:[`item.actions`]="{ item }">
                  <v-icon small color="error" @click="onClickDeleteImport(item)"> mdi-delete </v-icon>
                  <v-icon small color="nubeGreen" v-if="isClickSaveImport && isExistingvalue(item, 'uuid')">mdi-check-circle-outline</v-icon>
                  <v-icon
                    small
                    color="info"
                    @click="createPoint(item)"
                    v-if="isClickSaveImport"
                    :disabled="isClickSaveImport && isExistingvalue(item, 'uuid')">
                    mdi-cached
                  </v-icon>
                </template>
                <template v-slot:[`item.name`]="{ item, index }">
                  <v-text-field
                    v-if="isClickSaveImport && isExistingvalue(item, 'uuid')"
                    v-model="item.name"
                    single-line
                    style="width: 100px"
                    disabled
                  />
                  <v-text-field
                    v-else
                    @input="val => changeName(val, index)"
                    v-model="item.name"
                    single-line
                    style="width: 100px"
                    required
                    :rules="[() => !isExistingvalue(item, 'name') || 'Name Existed', rules.required]"
                  />
                </template>
                <template v-slot:[`item.register`]="{ item }">
                  <v-text-field
                    v-if="isClickSaveImport && isExistingvalue(item, 'uuid', item)"
                    v-model="item.register"
                    single-line
                    style="width: 100px"
                    disabled
                  />
                  <v-text-field
                    v-else
                    v-model="item.register"
                    type="number"
                    single-line
                    style="width: 100px"
                    required
                    :rules="[() => !isExistingvalue(item, 'register', item) || 'Register Existed', rules.required]"
                  />
                </template>
                <template v-slot:[`item.enable`]="{ item }">
                  <v-checkbox v-model="item.enable"/>
                </template>
                <template v-slot:[`item.function_code`]="{ item }">
                  <v-select
                    v-model="item.function_code"
                    :items="point_types"
                    required
                    :rules="[rules.required]"
                    v-on:change="$refs.form.resetValidation()"
                  ></v-select>
                </template>
                <template v-slot:[`item.value`]="{ item }">
                  <v-text-field v-model="item.value" type="number" single-line style="width: 100px"></v-text-field>
                </template>
                <template v-slot:[`item.priority_array_write`]>
                  <!-- {{
                    item.priority_array_write && typeof item.priority_array_write == 'object'
                      ? JSON.stringify(item.priority_array_write)
                      : item.priority_array_write
                  }} -->
                </template>
              </v-data-table>
            </v-form>
            <v-btn x-small class="mr-1 btn-height-22 white--text" color="secondary" @click="e1 = 1">Back</v-btn>
            <v-btn x-small class="mr-1 btn-height-22 white--text" color="primary" @click="saveImport" :disabled="!validWizard || savingImport">Save</v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <import-from-excel-dialog
      :is-open="dialogImportExcel"
      :on-close="() => (dialogImportExcel = false)"
      :on-confirm="convertExcelToJson"
    ></import-from-excel-dialog>

    <!-- Delete dialog -->
    <confirm-dialog
      :is-open="dialogDelete"
      :on-close="closeDelete"
      :on-confirm="confirm"
      dialog-text="Do you want to delete ?"
    ></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="350px">
      <v-card>
        <v-card-title>Mass Edit</v-card-title>
        <v-card-text>
          <v-select
            v-if="editField === 'data_type'"
            v-model="editAll[editField]"
            :items="point_data_types"
            :label="toUpperTextKey(editField)"
          ></v-select>
          <v-select
            v-if="editField === 'data_endian'"
            v-model="editAll[editField]"
            :items="point_data_endians"
            :label="toUpperTextKey(editField)"
          ></v-select>
          <v-checkbox
            v-if="typeof editAll[editField] === 'boolean'"
            v-model="editAll[editField]"
            :label="toUpperTextKey(editField)"
          />
          <v-text-field
            v-if="typeof editAll[editField] === 'number'"
            v-model.number="editAll[editField]"
            min="0"
            :label="toUpperTextKey(editField)"
            type="number"
            @input="numberChange()"
          ></v-text-field>
          <v-text-field
            v-if="
              typeof editAll[editField] === 'string' &&
              editField !== 'function_code' &&
              editField !== 'data_type' &&
              editField !== 'data_endian'
            "
            v-model="editAll[editField]"
            :label="toUpperTextKey(editField)"
          ></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Pre Made Point Dialog -->
    <v-dialog v-model="dialogPreMade" max-width="1000px">
      <v-card>
        <v-card-title>
          <div class="headline">Create Points</div>
        </v-card-title>
        <v-card-text style="max-height: 40vh; overflow: auto">
          <v-form v-model="valid" ref="form" lazy-validation>
            <v-row>
              <v-col cols="4">
                <template v-if="!modbusPoints.length"> No pre made points. </template>
                <template v-else>
                  <v-text-field
                    v-model="searchPrePoints"
                    append-icon="mdi-magnify"
                    label="Search"
                    single-line
                    hide-details
                    class="mb-1"
                  ></v-text-field>
                  <v-treeview
                    v-model="selectedPoints"
                    :items="modbusPoints"
                    selectable
                    return-object
                    open-all
                    class="text-left"
                    @input="updatedSelected"
                    activatable
                    ref="tree"
                    :search="searchPrePoints"
                  ></v-treeview>
                </template>
              </v-col>
              <v-divider vertical></v-divider>
              <v-col cols="8">
                <template v-if="!selectedPoints.length"> No points selected. </template>
                <template v-else>
                  <div v-for="(key, index) of Object.keys(selectedPointsShow)" :key="index" class="mb-4 pa-2">
                    <h3 class="mb-2" style="opacity: 0.6">{{ getDeviceNameByDeviceId(key) }}</h3>
                    <v-row>
                      <v-col cols="12" sm="6" md="6" v-for="(point, idx) of selectedPointsShow[key]" :key="idx">
                        <v-text-field
                          v-model="point.value.name"
                          :label="`pre point: ${point.name}`"
                          required
                          :rules="[() => !isExistingvalue(point.value, 'name') || 'Name Existed', rules.required]"
                        ></v-text-field>
                      </v-col> </v-row
                    ><br />
                    <v-divider></v-divider>
                  </div>
                </template>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogPreMade = false"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="savePoints" :disabled="!valid || selectedPoints.length == 0">
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Point Write Dialog -->
    <point-write-dialog
      :is-open="dialogPointWrite"
      :on-close="() => (dialogPointWrite = false)"
      :selected-point="editedItem"
      :api="'modbus'"
      :response-from-dialog="response => responseFromDialog(response)"
    ></point-write-dialog>
  </v-app>
</template>

<script>
import axios from '../../axios';
import * as xlsx from 'xlsx';
import _cloneDeep from 'lodash/cloneDeep';
import MqttConsole from '../Mqtt/MqttConsole';
import RightSidebar from '../Common/RightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import * as modbusServerService from '../../services/modbusServerService';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';
import PointWriteDialog from '../Common/PointWriteDialog';
import SystemStatusTable from '../Common/SystemStatusTable.vue';
import BreadcrumbsDirection from '../Common/BreadcrumbsDirection.vue';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import {
  RULES,
  TOPIC,
  POINT_DEFAULT,
  POINT_HEADERS,
  POINT_ADVANCED_HEADERS,
  POINT_TYPES,
  POINT_DATA_TYPES,
  POINT_DATA_ENDIANS,
  HISTORY_DATA_TYPES,
  POINT_EDIT_ALL_DEFAULT,
} from './modbusConstant';
import {
  isJsonString,
  copyToClipboard,
  exportJSON,
  exportCSV,
  exportExcel,
  toUpperTextKey,
  convertPointsValueToString,
  conventionNaming,
} from '../../utils/utils';

export default {
  components: {
    MqttConsole,
    RightSidebar,
    ConfirmDialog,
    ImportFromExcelDialog,
    PointWriteDialog,
    SystemStatusTable,
    BreadcrumbsDirection,
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    topic: TOPIC,
    pointHeaders: [...POINT_HEADERS],
    advancedHeaders: [...POINT_ADVANCED_HEADERS],
    point_types: [...POINT_TYPES],
    point_data_types: [...POINT_DATA_TYPES],
    point_data_endians: [...POINT_DATA_ENDIANS],
    history_data_types: [...HISTORY_DATA_TYPES],
    editAll: { ...POINT_EDIT_ALL_DEFAULT },
    editedItem: { ...POINT_DEFAULT },
    rules: { ...RULES },
    tableProps: { ...DATA_TABLE_PROPS },
    dialog: false,
    dialogDelete: false,
    dialogPointWrite: false,
    dialogImport: false,
    dialogImportExcel: false,
    dialogMassEdit: false,
    dialogPreMade: false,
    tab: 0,
    editedIndex: -1,
    action: '',
    device: {},
    valid: false,
    selected: [],
    dataImport: '',
    importArray: [],
    e1: 1,
    search: '',
    searchWizard: '',
    editField: '',
    intervalId: null,
    expanded: [],
    isClickSaveImport: false,
    dirs: [],
    advancedView: false,
    modbusPoints: [],
    selectedPoints: [],
    selectedPointsShow: [],
    searchPrePoints: '',
    validWizard: false,
    savingImport: false,
    loadingData: false,
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Create Point' : 'Edit Point';
    },
    debugSubscriptions() {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.modbus;
    },
    setting() {
      return this.$store.state.setting;
    },
    networksData() {
      return this.$store.state.mbNetworks;
    },
  },
  watch: {
    'dialog'(val) {
      val || this.close();
    },
    'dialogDelete'(val) {
      val || this.closeDelete();
    },
    '$route.params.id': function (val, oldVal) {
      if (val != oldVal) {
        this.getData();
      }
    },
    'selectedPoints'(val, oldVal) {
      val.forEach(point => {
        point.value.name = point.name;
      });
    },
  },
  beforeRouteLeave(to, from, next) {
    clearInterval(this.intervalId);
    this.$store.commit('showOverlay');
    axios.post(`mqtt/unsubscribe`, { topic: this.topic }).finally(() => {
      this.$store.commit('hideOverlay');

      next();
    });
  },
  mounted() {
    this.getModbusPoints();
    this.getData();
    //set auto refresh
    if (this.setting.refreshTime && this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(() => {
        this.getData();
        if (this.setting.toastAutoRefresh) {
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    async getData() {
      this.loadingData = true;
      this.device = await modbusServerService.fetchDevice(this.$route.params.id, { with_children: true });
      this.getDirs();
      this.device.points = this.device.points.map(item => {
        const keys = Object.keys(item.point_store);
        if (keys && keys.length > 0) {
          keys.forEach(key => {
            item = { ...item, [key]: item.point_store[key] };
          });
        }
        delete item.point_store;
        delete item.point_uuid;
        return item;
      });
      this.loadingData = false;
    },
    getDirs() {
      modbusServerService.fetchNetwork(this.device.network_uuid).then(data => {
        this.dirs = [{ text: 'NETWORKS', link: '/modbus/networks', disabled: false }];
        this.dirs.push({ text: `NET: ${data.name}`, link: `/modbus/networks/${data.uuid}/devices`, disabled: false });
        this.dirs.push({ text: `DEV: ${this.device.name}`, disabled: true });
      });
    },
    onClickCreateEditPoint(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign(
        {},
        this.editedIndex == -1 ? { ...POINT_DEFAULT, device_uuid: this.device.uuid } : item,
      );
      this.expanded = [];
      this.dialog = true;
      if (this.$refs.form) {
        this.$refs.form.resetValidation();
        // this.valid = false;
      }
    },
    onClickPreMadePoint(){
      this.dialogPreMade = true;
      this.selectedPoints = [];
      this.searchPrePoints = '';
    },
    save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          this.updatePoint(this.editedItem);
        } else {
          this.createPoint(this.editedItem);
        }
      }
    },
    async onClickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.editedItem = item;
    },
    async onClickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.device.points.indexOf(this.editedItem);
      this.device.points.splice(this.editedIndex, 1);
      await modbusServerService.removePoint(this.editedItem.uuid);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.editedItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, { ...POINT_DEFAULT, device_uuid: this.device.uuid });
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, { ...POINT_DEFAULT, device_uuid: this.device.uuid });
        this.editedIndex = -1;
      });
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport = false;
      this.$nextTick(() => {
        this.dataImport = '';
      });
    },
    async onClickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if (this.importArray && this.importArray.length > 0) {
        if (this.$refs.form_wizard.validate()) {
          this.savingImport = true;
          this.isClickSaveImport = true;
          try {
            await this.createMutiPoints(this.importArray);
            this.closeDialogImport();
            this.selected = [];
          } catch (ex) {
          } finally {
            this.savingImport = false;
          }
        }
      }
    },
    clickImportJson() {
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : '';
      this.e1 = 1;
    },
    convertExcelToJson(file) {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = event => {
        let data = event.target.result;
        let workbook = xlsx.read(data, { type: 'binary' });
        workbook.SheetNames.forEach(sheet => {
          let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
          const items = rowObject.map(i => {
            for (let key in i) {
              const newKey = key.trimRight('');
              const value = i[key];
              delete i[key];
              i = { ...i, [newKey]: value };
            }
            i.device_uuid = this.device.uuid;
            return i;
          });
          this.dataImport = JSON.stringify(items);
          this.dialogImportExcel = false;
          this.dialogImport = true;
          this.e1 = 1;
        });
      };
    },
    clickContinue() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(point => {
        delete point.uuid;
        delete point.value_raw;
        return point;
      });
      this.e1 = 2;
    },
    isExistingvalue(item, fieldName) {
      let value = item[fieldName];
      let points = _cloneDeep(this.device.points);
      if (this.editedIndex !== -1) {
        points.splice(this.editedIndex, 1); //when edit
      }
      if (fieldName === 'register') {
        value = Number(value);
        points = [...points].filter(p => p.function_code === item.function_code);
      }
      return points.find(p => p[fieldName] == value) != null;
    },
    clickJsonExport() {
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type) {
      const data = convertPointsValueToString(JSON.parse(JSON.stringify(array)));
      if (type === 'csv') {
        return exportCSV('points', data);
      }
      if (type === 'excel') {
        return exportExcel('points', data);
      }
      if (type === 'json') {
        return exportJSON('points', JSON.stringify(array));
      }
    },
    updatePoint(data) {
      data.fallback_value = !isNaN(parseFloat(data.fallback_value)) ? parseFloat(data.fallback_value) : null;
      modbusServerService.updatePoint(data.uuid, data).then(response => {
        const index = this.device.points.findIndex(n => n.uuid == response.uuid);
        Object.assign(this.device.points[index], response);
        this.$toasted.show('update success');
        this.close();
        this.expanded = [];
      });
    },
    async createPoint(data) {
      data.fallback_value = !isNaN(parseFloat(data.fallback_value)) ? parseFloat(data.fallback_value) : null;
      modbusServerService.createPoint(data).then(response => {
        this.device.points.push(response);
        if (this.importArray && this.importArray.length > 0) {
          const index = this.importArray.findIndex(n => n.name == response.name);
          if (index > -1) {
            Object.assign(this.importArray[index], response);
          }
        }
        this.$toasted.show('create success');
        this.close();
      });
    },
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'nubeYellow';
      } else {
        classColor = !item.enable ? 'nubeLightGrey' : '';
      }
      return classColor;
    },
    saveMassEdit(value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName) {
      if (this.selected.length < 1) {
        return this.$toasted.show('Please select item first!', { type: 'error' });
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = { ...POINT_EDIT_ALL_DEFAULT };
    },
    numberChange() {
      if(!this.editAll[this.editField] && this.editField !== 'fallback_value') return this.editAll[this.editField] = 0;
    },
    cancelEdit() {},
    openEdit(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    expandItem({ item }) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    onClickRefresh() {
      this.getData();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    getModbusPoints() {
      axios.get('modbus').then(r => {
        if (!r || !r.length) return;
        const devices = r;
        this.modbusPoints = [{ id: 1, name: 'Select All', children: [] }];
        devices.forEach(device => {
          if (device.points && device.points.length > 0) {
            const deviceChild = [];
            device.points.forEach(point => {
              deviceChild.push({ id: point._id, name: point.name, value: point });
            });
            this.modbusPoints[0].children.push({
              id: device._id,
              name: `Device: ${device.name}`,
              children: deviceChild,
            });
          }
        });
      });
    },
    async savePoints() {
      if (this.$refs.form.validate()) {
        const points = this.selectedPoints.map(p => p.value);
        points.forEach(point => {
          if (point.uuid) delete point.uuid;
        });
        this.dialogPreMade = false;
        await this.createMutiPoints(points);
      }
    },
    async createMutiPoints(points) {
      const promises = [];
      for (const item of points) {
        item.device_uuid = this.device.uuid; //update to the current device_uuid
        item.register_length = item.register_length ? item.register_length : 0;
        if (item.priority_array_write && typeof item.priority_array_write == 'string') {
          item.priority_array_write = JSON.parse(item.priority_array_write);
        }
        if (!item.uuid) {
          promises.push(this.createPoint(item));
        }
      }
      await Promise.all(promises);
    },
    updatedSelected() {
      this.selectedPointsShow = this.selectedPoints.reduce((grs, currPoint) => {
        grs[currPoint.value.device_id]
          ? grs[currPoint.value.device_id].push(currPoint)
          : (grs[currPoint.value.device_id] = [currPoint]);
        return grs;
      }, {});
    },
    getDeviceNameByDeviceId(device_id) {
      const devices = this.modbusPoints[0].children;
      return devices.find(d => d.id == device_id).name;
    },
    changeName(val, index) {
      if (index > -1) {
        this.importArray[index].name = conventionNaming(val, this.setting); //for wizzard dialog only
      } else {
        this.editedItem.name = conventionNaming(val, this.setting); //for create-edit dialog only
      }
    },
    openPointWriteDialog(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogPointWrite = true;
    },
    responseFromDialog(point_updated) {
      const index = this.device.points.findIndex(p => p.uuid == point_updated.uuid);
      Object.assign(this.device.points[index], point_updated);
    },
  },
};
</script>
<style lang="scss">
#no-background-hover::before {
  background-color: transparent !important;
  width: -webkit-fill-available;
  padding: 0;
}
</style>
